<template>
  <header class="db-header">
    <div class="db-header__top d-flex align-items-center justify-content-between">
      <div class="d-flex align-items-center">
        <div 
          class="db-header__logo"
          :class="{'hide':dbSidebarVisible}"
        >
          <img
            class="d-block ml-4 mt-2"
            src="@/assets/img/logo__white.png" 
            alt="Fine.Trading" 
            width="115px">
        </div>
        <div
          style="width:60px;height:60px;"
          class="d-flex justify-content-center align-items-center"
        >
          <i @click="TOGGLE_DB_SIDEBAR" class="icon ion-navicon pointer"></i>
        </div>
      </div>
      <nav class="d-flex align-items-center">
          <div class="db-nav__link fz-24 px-2">
              <i class="icon ion-ios-email-outline tx-24"></i>
          </div>
          <div class="db-nav__link fz-24 px-2">
              <i class="icon ion-ios-bell-outline tx-24"></i>
          </div>
          <div class="db-nav__user" style="position:relative;">
            <div
              @click="popUpVisible = !popUpVisible"
              data-popup='true'
              class="db-nav__link pointer d-flex align-items-center px-3"
            >
            <p v-if="!USER_INFO"></p>
            <p v-else data-popup='true' class="mr-2">{{USER_INFO.email}}</p>
            <img
              src="http://via.placeholder.com/64x64"
              class="db-nav__link-pofile rounded-circle"
              data-popup='true'
              width="32px"
              alt=""
            >
            </div>
            <PopUp 
              @close="popUpVisible=false"
              :visible="popUpVisible"
            >
              <span
                class="popUp__item pointer"
              > 
                <i class="fas fa-cog mr-2"></i> Настройки 
              </span>
              <span
                @click="logout"
                class="popUp__item pointer"
              > 
                <i class="fas fa-power-off mr-2"></i> Выйти 
              </span>
            </PopUp>
          </div>
          <button
            @click="TOGGLE_DB_RIGHTBAR"
            class="db-chat__btn fz-24"
          >
            <i class="icon ion-ios-chatboxes-outline"></i>
          </button>
      </nav>
    </div>
   
  </header>
</template>

<script>

import { mapState, mapMutations, mapGetters } from 'vuex';

export default {
  name: 'db-Header',
  data() {
    return {
      popUpVisible: false
    }
  },
  computed: {
    ...mapState([
      'dbSidebarVisible',
    ]),
    ...mapGetters([
      'USER_INFO'
    ])
  },
  components: {
    PopUp: () => import('@/components/popUp'),
  },
  methods: {
    ...mapMutations([
      'TOGGLE_DB_SIDEBAR',
      'TOGGLE_DB_RIGHTBAR',
      'LOGOUT'
    ]),
    logout() {
      this.LOGOUT()
      this.$router.push('/')
    }
  }
}
</script>

<style lang="scss">
 @import '@/assets/styles/utils/vars.scss';

  .db-header {
    position: fixed;
    height: 60px;
    top: 0;
    left: 0;
    right: 0;
    z-index: 1030;
    background-color: #fff;
    transition: 0.5s ease margin;

    &__logo {
      position: relative;
      width: 230px;
      height: 60px;
      background: $mainFontColor;
      transition: 0.3s ease width;
      z-index: 1240;
      &.hide {
        width: 0px;
      }
    }
  }

  .db-nav__link {
    opacity: 0.7;
    transition: all 0.2s ease-in-out;
    &:hover {
      opacity: 1;
    }
  }

  .db-chat__btn {
    width: 60px;
    height: 60px;
    border-left: 1px solid rgba(0, 0, 0, 0.15);
    transition: all 0.2s ease-in-out;
    opacity: 0.6;
    background: #fff;
    &:hover {
      opacity: 0.8;
    }
  }

</style>
